import mkbImgCut from '../mkb-img-cut/mkb-img-cut.vue';
export default {
	data() {
		return {
			idx:'-1',
			list:[],
			listItem:''
		};
	},
	components:{
		mkbImgCut
	},
	methods:{
		// 选择风格
		select(index,item){
			this.idx = index;
			this.listItem = item;
			this.$emit('priceManner',this.idx,this.listItem);
		}
	},
	mounted(){
		this.$emit('priceManner',this.idx);
		let categorys;
		if (uni.getStorageSync("catalogues")) {
			categorys = uni.getStorageSync("catalogues").categorys;
		} else if (this.$store.state.catalogues) {
			categorys = this.$store.state.catalogues.categorys;
		}
		console.log(categorys)
		if (categorys && categorys.length > 0) {
			let list= categorys.filter((item)=>{
				return item.type == 2;
			})
			this.list = list;
		}
		console.log(this.list)
	}
}
